.item{
    color: #F3F2DC;
    padding: 5px;
    margin: 5px;
    transition: 300ms;
    display: flex;
    align-items: center;
}

.backgroundIcon {
    font-size: 12px;
    height: 35px;
    width: 35px;
    margin-left: 5px;
    background-color: #424B50;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.text {
    font-size: 16px;
}

