.navUser {
   display: flex;
      justify-content: center;
      align-items: center;
 
}

.userName {
   color: #262B2E;
   font-size: 14px;
   margin-right: 4px;
}

.userImgContainer {
   display: flex;
   align-items: center;
}

.userImg {
   width: 20px;
   height: 20px;
   border: 2px solid #262B2E;
   border-radius: 50%;
   font-size: 10px;
   display: flex;
      justify-content: center;
      align-items: center;

   margin-right: 4px;
}

.notifications{
   position: relative;
   font-size: 18px;
   color: #262B2E;
   margin-right: 4px;  
}

.No {
   color: #F3F2DC;
   position: absolute;
   width: 9px;
   height: 9px;
   background-color: #E57462;
   border-radius: 50%;
   font-size: 8px;
   display: flex;
   justify-content: center;
   align-items: center;

}

.dropdown{
   color: #262B2E;
   font-size: 28px;
   padding-bottom: 9px;
}

.open {
   color: #a1a1a1;
}

.dropdownOpen {
   background-color: #262B2E;
   display: flex;
   flex-direction: column;
   position: absolute;
   right: 0;
   top: 80px;
   border-radius: 10px;
}

@media screen and(min-width: 768px) {
   .navUser{ 
      position: absolute;
      top: 100px;
      right: 100px;
   }
   
}